import { Component, output, input } from '@angular/core';
import { MultiSelectChangeEvent, MultiSelectModule } from 'primeng/multiselect';
import { FilterOption } from '../../../../models';
import { FormsModule } from '@angular/forms';

@Component({
  selector: 'app-toggle-filter',
  imports: [MultiSelectModule, FormsModule],
  templateUrl: './toggle-filter.component.html',
  styleUrl: './toggle-filter.component.scss',
})
export class ToggleFilterComponent {
  readonly options = input<FilterOption[]>([]);
  readonly filterChange = output<string[]>();
  selectedValues: (string | number)[] = [];

  onSelectionChange(event: MultiSelectChangeEvent): void {
    this.filterChange.emit(event.value as string[]);
  }
}
