import {
  Component,
  input,
  output,
  OnInit,
  DestroyRef,
  inject,
} from '@angular/core';
import { InputTextModule } from 'primeng/inputtext';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { debounceTime, Subject } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { isDefined } from '../../../../utils';
import { FilterValue } from '../../../../models';

@Component({
  selector: 'app-text-filter',
  standalone: true,
  imports: [InputTextModule, FormsModule, CommonModule],
  templateUrl: './text-filter.component.html',
  styleUrl: './text-filter.component.scss',
})
export class TextFilterComponent implements OnInit {
  private destroyRef = inject(DestroyRef);

  // Input signal for initial value
  readonly initialValue = input<FilterValue>();

  // Output event for filter changes
  readonly filterChange = output<string>();

  // Filter input value
  filterValue = '';

  // Subject for handling debounced input
  private inputSubject = new Subject<string>();

  ngOnInit(): void {
    // Set initial value if provided
    if (isDefined(this.initialValue())) {
      const value = this.initialValue();
      if (typeof value === 'string') {
        this.filterValue = value;
      }
    }

    // Set up debounced input handling
    this.inputSubject
      .pipe(
        debounceTime(300), // Wait for 300ms pause in typing
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((value) => {
        this.filterChange.emit(value);
      });
  }

  // Handle input changes
  onInputChange(event: Event): void {
    const value = (event.target as HTMLInputElement).value;
    this.inputSubject.next(value);
  }

  // Method to update value from parent component
  updateValue(value: FilterValue): void {
    if (isDefined(value) && typeof value === 'string') {
      this.filterValue = value;
    } else {
      this.filterValue = '';
    }
  }

  // Clear the filter
  clearFilter(): void {
    this.filterValue = '';
    this.filterChange.emit('');
  }
}
