import { Component, output, input, ViewChild } from '@angular/core';
import { FilterOption, FilterValue } from '../../../../models';
import { ToggleFilterComponent } from '../toggle-filter/toggle-filter.component';
import { MultiselectFilterComponent } from '../multiselect-filter/multiselect-filter.component';
import { TextFilterComponent } from '../text-filter/text-filter.component';

@Component({
  selector: 'app-filter-wrapper',
  imports: [
    ToggleFilterComponent,
    MultiselectFilterComponent,
    TextFilterComponent,
  ],
  templateUrl: './filter-wrapper.component.html',
  styleUrl: './filter-wrapper.component.scss',
})
export class FilterWrapperComponent {
  @ViewChild(MultiselectFilterComponent)
  multiselectFilter?: MultiselectFilterComponent;
  readonly field = input.required<string>();
  readonly filterType = input.required<string>();
  readonly options = input<FilterOption[]>([]);
  readonly defaultSelected = input<string[]>([]);
  readonly initialValue = input<FilterValue>();
  readonly filterChange = output<FilterValue>();

  onFilterChange(value: FilterValue): void {
    this.filterChange.emit(value);
  }

  setInitialValue(value: FilterValue): void {
    if (value === undefined) return;

    setTimeout(() => {
      if (this.filterType() === 'multiselect' && this.multiselectFilter) {
        this.multiselectFilter.updateValue(value);
      }
    });
  }
}
