/**
 * Utility functions for formatting different types of measurement units
 */
export class UnitFormatters {
  /**
   * Format date units to readable format
   */
  static formatDateUnit(unit: string): string {
    const dateFormats: Record<string, string> = {
      yyyymmdd1: 'yyyy/mm/dd',
      yyyymmdd2: 'yyyy.mm.dd',
      yyyymmdd3: 'yyyy-mm-dd',
      ddmmyyyy1: 'dd/mm/yyyy',
      ddmmyyyy2: 'dd.mm.yyyy',
      ddmmyyyy3: 'dd-mm-yyyy',
      dmyyyy1: 'd/m/yyyy',
      dmyyyy2: 'd.m.yyyy',
      dmyyyy3: 'd-m-yyyy',
      mmddyyyy1: 'mm/dd/yyyy',
      mdyyyy1: 'm/d/yyyy',
    };

    return dateFormats[unit] || unit;
  }

  /**
   * Format time units to readable format
   */
  static formatTimeUnit(unit: string): string {
    const timeFormats: Record<string, string> = {
      HHmmss: 'HH:mm:ss',
      Hmmss: 'H:mm:ss',
      hmmssa: 'h:mm:ss AM/PM',
    };

    return timeFormats[unit] || unit;
  }

  /**
   * Format distance/altitude units to readable format
   */
  static formatDistanceUnit(unit: string): string {
    const distanceFormats: Record<string, string> = {
      kilometres: 'Kilometres',
      meters: 'Metres',
      feet: 'Feet',
      inches: 'Inches',
      miles: 'Miles',
      nauticalMiles: 'Nautical Miles',
      yards: 'Yards',
    };

    return distanceFormats[unit] || UnitFormatters.capitalizeFirstLetter(unit);
  }

  /**
   * Format speed units to readable format
   */
  static formatSpeedUnit(unit: string): string {
    const speedFormats: Record<string, string> = {
      kilometresPerHour: 'Kilometres Per Hour',
      metresPerSecond: 'Metres Per Second',
      milesPerHour: 'Miles Per Hour',
      knots: 'Knots',
    };

    return speedFormats[unit] || UnitFormatters.decamelize(unit);
  }

  /**
   * Format volume units to readable format
   */
  static formatVolumeUnit(unit: string): string {
    const volumeFormats: Record<string, string> = {
      litres: 'Litres',
      cubicMetres: 'Cubic Metres',
      cubicFeet: 'Cubic Feet',
      gallons: 'Gallons (UK)',
      ounces: 'Ounces (UK)',
      gallonsUs: 'Gallons (US)',
      ouncesUs: 'Ounces (US)',
    };

    return volumeFormats[unit] || UnitFormatters.decamelize(unit);
  }

  /**
   * Format weight units to readable format
   */
  static formatWeightUnit(unit: string): string {
    const weightFormats: Record<string, string> = {
      kilograms: 'Kilograms',
      ounces: 'Ounces (UK)',
      pounds: 'Pounds',
      tonnes: 'Tonnes',
    };

    return weightFormats[unit] || UnitFormatters.decamelize(unit);
  }

  /**
   * Format area units to readable format
   */
  static formatAreaUnit(unit: string): string {
    const areaFormats: Record<string, string> = {
      squareKilometres: 'Square Kilometres',
      acres: 'Acres',
      hectares: 'Hectares',
      squareFeet: 'Square Feet',
      squareMetres: 'Square Metres',
      squareInches: 'Square Inches',
      squareMiles: 'Square Miles',
      squareYards: 'Square Yards',
    };

    return areaFormats[unit] || UnitFormatters.decamelize(unit);
  }

  /**
   * Format temperature units to readable format
   */
  static formatTemperatureUnit(unit: string): string {
    const tempFormats: Record<string, string> = {
      celsius: 'Celsius',
      fahrenheit: 'Fahrenheit',
    };

    return tempFormats[unit] || UnitFormatters.capitalizeFirstLetter(unit);
  }

  /**
   * Helper method to capitalize the first letter of a string
   */
  static capitalizeFirstLetter(str: string): string {
    if (!str) return '';
    return str.charAt(0).toUpperCase() + str.slice(1);
  }

  /**
   * Helper method to convert camelCase to Title Case With Spaces
   */
  static decamelize(str: string): string {
    if (!str) return '';

    // First capitalize the first letter
    const result = str
      // Split by capital letters
      .replace(/([A-Z])/g, ' $1')
      // Trim any extra spaces
      .trim();

    // Capitalize the first letter and return
    return UnitFormatters.capitalizeFirstLetter(result);
  }
}
