/**
 * Represents user preferences for measurement units across different categories
 */
export interface UnitPreferences {
  distanceUnit?: string;
  altitudeUnit?: string;
  speedUnit?: string;
  areaUnit?: string;
  volumeUnit?: string;
  weightUnit?: string;
  dateUnit?: string;
  temperatureUnit?: string;
  timeUnit?: string;
}

/**
 * Format options for date/time display
 */
export interface DateTimeFormatOptions {
  long?: string;
  short?: string;
}

/**
 * Definition for a specific measurement unit
 */
export interface UnitDefinition extends DateTimeFormatOptions {
  multiplier?: number;
  unit: string;
  symbol?: string;
}

/**
 * Configuration for a category of measurement units
 */
export interface UnitTypeConfig {
  units?: Record<string, UnitDefinition>;
  value?: (raw: number | string, unit: UnitDefinition) => number | string;
  format?: (
    value: number | string,
    unit: UnitDefinition,
    variant?: string,
  ) => string;
  toBackend?: (value: number | string, unit: UnitDefinition) => number | string;
  fromBackend?: (
    value: number,
    unit: UnitDefinition,
    roundingDigits?: number,
  ) => number;
  error?: string;
}

/**
 * Supported measurement unit categories
 */
export enum MeasurementUnitType {
  DISTANCE = 'distance',
  ALTITUDE = 'altitude',
  SPEED = 'speed',
  AREA = 'area',
  VOLUME = 'volume',
  WEIGHT = 'weight',
  DATE = 'date',
  TEMPERATURE = 'temperature',
  TIME = 'time',
}

/**
 * Represents a formatted measurement value
 */
export interface FormattedMeasurement {
  value: number | string | null;
  format: string;
  unit: string;
  multiplier: number;
  error?: string;
}

export const DATE_FORMATS = {
  DATE_TIME_SECONDS: 'DD/MM/YYYY HH:mm:ss',
  DATE_ONLY: 'DD/MM/YYYY',
  TIME_ONLY_SECONDS: 'HH:mm:ss',
  // Add other formats as needed
};
