import { Component, input, OnInit, output } from '@angular/core';
import { FilterOption, FilterValue } from '../../../../models';
import { MultiSelectChangeEvent, MultiSelectModule } from 'primeng/multiselect';
import { FormsModule } from '@angular/forms';
import { isDefined } from '../../../../utils';

@Component({
  selector: 'app-multiselect-filter',
  imports: [MultiSelectModule, FormsModule],
  templateUrl: './multiselect-filter.component.html',
  styleUrl: './multiselect-filter.component.scss',
})
export class MultiselectFilterComponent implements OnInit {
  readonly defaultSelected = input<string[]>([]);
  readonly options = input<FilterOption[]>([]);
  readonly initialValue = input<FilterValue>();
  readonly filterChange = output<string[]>();

  selectedValues: (string | number)[] = [];

  ngOnInit() {
    // Initialize with initialValue if provided
    if (isDefined(this.initialValue()) && Array.isArray(this.initialValue())) {
      this.setSelectedValues(this.initialValue() as (string | number)[]);
    }
  }

  // Method to update value from parent
  updateValue(value: FilterValue): void {
    if (isDefined(value) && Array.isArray(value)) {
      this.setSelectedValues(value as (string | number)[]);
    }
  }

  // Helper method to set selectedValues and update option selected states
  private setSelectedValues(values: (string | number)[]): void {
    this.selectedValues = [...values];

    const currentOptions = this.options();
    if (currentOptions.length > 0) {
      currentOptions.forEach((option) => {
        option.selected = this.selectedValues.includes(option.value as string);
      });
    }
  }

  onSelectionChange(event: MultiSelectChangeEvent): void {
    this.filterChange.emit(event.value as string[]);
  }
}
