export interface FieldConfiguration {
  id: string;
  display: string;
  title?: string;
  icon?: string;
  iconStyle?: object;
}

/** Configuration for the modal used by list fields */
export interface ListFieldModalConfig<TListItem = unknown> {
  /** The component to render inside the modal (usually ModalListFormComponent) */
  modalComponent: Type<unknown>; // Use Type<unknown>
  /** Function or string to generate the modal title (e.g., "Add Item", "Edit Item") */
  modalTitle: string | ((item?: TListItem) => string); // Use TListItem
  /** Function or array defining the FormFieldConfig[] for the modal's internal form */
  modalFields: FormFieldConfig[] | ((item?: TListItem) => FormFieldConfig[]); // Use TListItem
  /** Optional: Function to transform the raw list item data for display in the list */
  listItemFormatter?: (
    item: TListItem, // Use TListItem
  ) => FieldConfiguration;
  /** Optional: Function to provide default values when adding a new item */
  defaultItemProvider?: () => Partial<TListItem>;
  /** Optional: Label for the 'Add' button */
  addButtonLabel?: string;
  /** Optional: Subheader text to display within the modal */
  subHeader?: string;
  /** Optional: Flag to hide the save notification after adding or updating an item */
  /** This is useful when you want to control the visibility of the notification manually */
  hideSaveNotification?: boolean;
  /** Optional: Flag to show or hide the edit button for list items (default: false) */
  showEditButton?: boolean;
  // --- Properties specific to 'treeSplitButtonList' type ---
  /** Optional: Custom function to transform a selected TreeNode into the item to be added to the list. */
  transformTreeNodeToItem?: (node: TreeNode) => TListItem;
  /**
   * Optional: Custom function to call when the modal's primary action (e.g., "Add", "Update") is triggered.
   * This function receives the submitted item and the DynamicDialogRef.
   * It can perform asynchronous operations.
   * If the function returns `false` or a Promise that resolves to `false`, the dialog will NOT close automatically.
   * Otherwise, or if it returns void/undefined/true, the dialog will close with the submitted item.
   * To manually close the dialog with a specific result, use `dialogRef.close(result)`.
   * To change content, you'd typically update `dialogRef.config.data` and have the `modalComponent` react to it.
   */
  onSubmit?: (
    item: TListItem,
    dialogRef: DynamicDialogRef,
  ) => Promise<boolean | void> | boolean | void;
  /**
   * Optional: Custom function to call when an item is about to be deleted from the list.
   * This function receives the item to be deleted, its index, and the field configuration.
   * It can perform asynchronous operations (e.g., API call).
   * If the function returns `false` or a Promise that resolves to `false`,
   * the default client-side deletion (splicing the array) will be skipped.
   * This allows the custom handler to take full control of the deletion process,
   * including updating the form control's value if necessary.
   * If it returns `true`, `void`, or a Promise resolving to such, the default deletion proceeds.
   */
  onDeleteItem?: (
    item: TListItem,
    index?: number,
    field?: FormFieldConfig,
  ) => Promise<boolean | void> | boolean | void;
}
export interface FormFieldConfig<T = unknown> {
  key: string;
  type: FieldType;
  name: string;
  label?: string;
  value?: T | T[];
  loading?: boolean | (() => boolean);
  autocompleteLoading?: boolean | (() => boolean);
  readonly?: boolean;
  validations?: ValidationRule[];
  options?: SelectOption[];
  treeNodeOptions?: TreeNode[];
  dependsOn?: string[];
  showWhen?: (formValue: unknown) => boolean;
  template?: string;
  fields?: FormFieldConfig[];
  props?: FormFieldProps<T>;
  validateWhen?: (formValue: unknown) => boolean;
  persistKey?: string;
  required?: boolean;
  visible?: boolean;
  disabled?: boolean;
  errorMessages?: Record<string, string>;
  placeholder?: string;
  hint?: string;
  styleClass?: string;
  inputStyleClass?: string;
  dependencies?: FieldDependency[];
  layout?: 'horizontal' | 'vertical';
  labelWidth?: string;
  events?: FormFieldEvents;
  style?: Record<string, string>;
  hidden?: boolean;

  // For conditional fields
  conditional?: boolean;
  conditions?: Record<string, FormFieldConfig[]>;
  previousValue?: unknown;
}

export interface FormFieldEvents {
  onSearch?: (query: string) => void;
  onChange?: (value: string) => void;
}

export interface FieldDependency {
  field: string;
  value: unknown;
  action: 'show' | 'hide' | 'enable' | 'disable';
  invertMatch?: boolean;
}

export type DependencyValue = string | number | boolean | null;

export type GroupValues = Record<
  string,
  string | number | boolean | null | undefined
>;

export type FormState = Record<string, unknown>;

export type FormValue = Record<string, unknown>;

export type FieldType =
  | 'text'
  | 'number'
  | 'dropdown'
  | 'date'
  | 'checkbox'
  | 'radio'
  | 'textarea'
  | 'multiselect'
  | 'rating'
  | 'slider'
  | 'toggleButton'
  | 'array'
  | 'group'
  | 'colorPicker'
  | 'password'
  | 'knob'
  | 'file'
  | 'editor'
  | 'mask'
  | 'custom'
  | 'autocomplete'
  | 'listbox'
  | 'treeselect'
  | 'modalList'
  | 'chips'
  | 'eventFilter'
  | 'assetFilter'
  | 'treeSplitButtonList'
  | 'icon-selector'
  | 'toggle-switch'
  | 'asset-filter';

export type GroupTypes = 'costcentres' | 'groups' | 'types' | 'categories';

export interface ValidationRule {
  name: string;
  validator: ValidatorFn;
  message?: string;
  params?: Record<string, unknown>;
}

export interface FormAction {
  label: string;
  type?: 'submit' | 'button' | 'reset' | 'cancel';
  severity?: 'primary' | 'secondary' | 'info' | 'warn' | 'danger';
  outlined?: boolean;
  icon?: string;
  styleClass?: string;
  disabled?: boolean | ((form?: FormGroup) => boolean);
  visible?: boolean | ((form?: FormGroup) => boolean);
  action?: (form: FormGroup) => void;
  loading?: () => boolean;
}

export interface SelectOption<T = unknown> {
  label: string;
  value: T;
  disabled?: boolean;
  icon?: string;
}

export interface ColorOption {
  label: string;
  value: string;
}

export interface FormFieldProps<T = unknown> extends Record<string, unknown> {
  // Common Prime options
  placeholder?: string;
  icon?: string;
  iconPos?: 'left' | 'right';
  buttonClass?: string;
  inputClass?: string;
  mask?: string;
  showClear?: boolean;
  autoResize?: boolean;
  showButtons?: boolean;
  buttonLayout?: string;
  suffix?: string;

  // Calendar specific
  dateFormat?: string;
  showTime?: boolean;
  showSeconds?: boolean;
  timeOnly?: boolean;

  // File upload specific
  mode?: 'basic' | 'advanced';
  accept?: string;
  maxFileSize?: number;

  // Password specific
  feedback?: boolean;
  toggleMask?: boolean;

  // Editor specific
  modules?: Record<string, unknown>;

  // Dropdown/Multiselect specific
  filter?: boolean;
  filterBy?: string;

  // Slider specific
  min?: number;
  max?: number;
  step?: number;

  // Autocomplete specific
  suggestions?: unknown[] | (() => unknown[]) | null;
  forceSelection?: boolean;
  field?: string;
  completeOnFocus?: boolean;
  autocomplete?: string | null;
  typeahead?: boolean;
  delay?: number;
  showEmptyMessage?: boolean;
  emptyMessage?: string;
  dropdown?: boolean;

  // Select specific
  appendTo?: string | HTMLElement;

  // Listbox specific
  checkmark?: boolean;
  checkbox?: boolean;
  multiple?: boolean;

  // Tree Select specific
  selectionMode?: 'single' | 'multiple' | 'checkbox';

  // --- Properties specific to 'modalList' type ---
  /** Configuration for the modal used for adding/editing list items */
  modalConfig?: ListFieldModalConfig<T>;
  /** Optional: Custom template for rendering list items in the main form */
  listItemTemplate?: TemplateRef<{ $implicit: T }>; // Context for the item with the correct generic type
  addButtonLabel?: string; // Can be reused

  // --- Properties specific to 'treeSplitButtonList' type ---
  /** Optional: Custom function to transform a selected TreeNode into the item to be added to the list. */
  transformTreeNodeToItem?: (node: TreeNode) => T;
  listItemFormatter?: (item: T) => {
    id: string;
    display: string;
    title?: string;
    icon?: string;
  };
  // Number specific
  minFractionDigits?: number;
  maxFractionDigits?: number;

  // Asset Filter specific
  multiSelect?: boolean;
  showGroupTabs?: boolean;
  allowedAssetTypes?: string[];

  format?: 'hex' | 'rgb' | 'hsb';
  groupTypes?: GroupTypes[];
  colorOptions?: ColorOption[];
  filterValues?: Record<string, unknown>;
  ownerId?: string; // For asset filter

  // Event filter specific
  filterService?: EventFilterService;
  requireActorBeforeAction?: boolean;
}

export function createTextField(
  key: string,
  label: string,
  value: string | undefined | null,
  options: {
    name?: string;
    required?: boolean;
    validations?: ValidationRule[];
    layout?: 'horizontal' | 'vertical';
    labelWidth?: string;
    placeholder?: string;
    props?: FormFieldProps;
    events?: FormFieldEvents;
    hint?: string;
    disabled?: boolean;
    hidden?: boolean;
    readonly?: boolean;
  } = {},
): FormFieldConfig {
  if (!isString(key)) {
    throw new Error('Key must be a string');
  }

  if (!isString(label)) {
    throw new Error('Label must be a string');
  }

  return {
    key,
    name: isString(options.name) ? options.name : '',
    type: 'text',
    label,
    layout: isDefined(options.layout) ? options.layout : 'horizontal',
    labelWidth: isString(options.labelWidth) ? options.labelWidth : '1/3',
    validations: Array.isArray(options.validations) ? options.validations : [],
    value: isDefined(value) ? value : undefined,
    placeholder: isString(options.placeholder)
      ? options.placeholder
      : undefined,
    props: isObject(options.props)
      ? {
          ...options.props,
          typeahead: isDefined(options.props?.typeahead)
            ? options.props?.typeahead
            : true,
        }
      : undefined,
    events: isObject(options.events) ? options.events : undefined,
    hint: isString(options.hint) ? options.hint : undefined,
    hidden: isDefined(options.hidden) ? options.hidden : false,
    readonly: isDefined(options.readonly) ? options.readonly : false,
  };
}

