export function createAutocompleteField(
  key: string,
  label: string,
  value: string | string[] | IdName | undefined | null,
  suggestions: unknown[] | (() => unknown[]) | undefined,
  onSearch: (searchTerm: string) => void,
  options: {
    name?: string;
    validations?: ValidationRule[];
    layout?: 'horizontal' | 'vertical';
    labelWidth?: string;
    placeholder?: string;
    props?: FormFieldProps;
    events?: FormFieldEvents;
    hint?: string;
    autocompleteLoading?: () => boolean;
  } = {},
): FormFieldConfig {
  if (!isString(key)) {
    throw new Error('Key must be a string');
  }

  if (!isString(label)) {
    throw new Error('Label must be a string');
  }

  return {
    key,
    name: isString(options.name) ? options.name : '',
    type: 'autocomplete',
    label,
    layout: isString(options.layout) ? options.layout : 'horizontal',
    labelWidth: isString(options.labelWidth) ? options.labelWidth : '1/3',
    validations: Array.isArray(options.validations) ? options.validations : [],
    value: isDefined(value) ? value : undefined,
    placeholder: isString(options.placeholder)
      ? options.placeholder
      : undefined,
    props: {
      ...(options.props || {}),
      dropdown: options.props?.dropdown ?? true,
      suggestions: suggestions,
      placeholder: 'Search for clients...',
    },
    events: {
      ...(options.events || {}),
      onSearch,
    },
    hint: options.hint,
    autocompleteLoading: isDefined(options.autocompleteLoading)
      ? options.autocompleteLoading
      : false,
  };
}

