/**
 * Icon configuration for entity types
 * Supports PrimeNG icons (preferred) and FontAwesome icons (fallback)
 */

export interface EntityIconConfig {
  /** PrimeNG icon class (e.g., 'pi pi-user') - preferred */
  primeIcon?: string;
  /** FontAwesome icon class (e.g., 'fas fa-building') - fallback */
  faIcon?: string;
  /** Icon color/theme hint (optional) */
  colorHint?: string;
}

