/**
 * Default icon configuration when entity type is not found in map
 */
export const DEFAULT_ENTITY_ICON: EntityIconConfig = {
  primeIcon: 'pi pi-circle',
  colorHint: 'secondary',
};

/**
 * Get icon configuration for an entity type
 * @param entityType - The entity type (case-insensitive)
 * @returns Icon configuration with either PrimeNG or FontAwesome icon
 */
export function getEntityIcon(entityType: string): EntityIconConfig {
  if (!entityType) {
    return DEFAULT_ENTITY_ICON;
  }

  const normalizedType = entityType.toLowerCase().trim();
  const iconConfig = ENTITY_ICON_MAP[normalizedType];
  return iconConfig ?? DEFAULT_ENTITY_ICON;
}

/**
 * Get the icon class string for an entity type
 * @param entityType - The entity type
 * @returns Complete icon class string (e.g., 'pi pi-user' or 'fas fa-building')
 */
export function getEntityIconClass(entityType: string): string {
  const config = getEntityIcon(entityType);

  if (config.primeIcon !== undefined && config.primeIcon.length > 0) {
    return config.primeIcon;
  }

  if (config.faIcon !== undefined && config.faIcon.length > 0) {
    return config.faIcon;
  }

  return DEFAULT_ENTITY_ICON.primeIcon ?? 'pi pi-circle';
}

/**
 * Check if entity uses FontAwesome icon (vs PrimeNG)
 * @param entityType - The entity type
 * @returns True if using FontAwesome, false if using PrimeNG
 */
export function isEntityIconFontAwesome(entityType: string): boolean {
  const config = getEntityIcon(entityType);
  const hasPrimeIcon =
    config.primeIcon !== undefined && config.primeIcon.length > 0;
  const hasFaIcon = config.faIcon !== undefined && config.faIcon.length > 0;
  return !hasPrimeIcon && hasFaIcon;
}
