import {
  Component,
  computed,
  effect,
  input,
  output,
  QueryList,
  TemplateRef,
  ViewChild,
  ViewChildren,
} from '@angular/core';

import {
  Table,
  TableLazyLoadEvent,
  TableModule,
  TablePageEvent,
  TableRowSelectEvent,
  TableRowUnSelectEvent,
} from 'primeng/table';
import { ContextMenuModule } from 'primeng/contextmenu';
import { MenuItem } from 'primeng/api';

import {
  FilterChangeEvent,
  FilterValue,
  GridColumn,
  GridExpandedRow,
  GridRowData,
  GridSelection,
} from '../../models';
import {
  DEFAULT_PAGE_SIZE,
  DEFAULT_PAGE_SIZE_OPTIONS,
} from '../../../core/models';
import { DirectiveSwitchDirective } from '../../directives';
import { FilterWrapperComponent } from './filters/filter-wrapper/filter-wrapper.component';
import { CommonModule } from '@angular/common';
import { isDefined, isNullOrUndefined } from '../../utils';

@Component({
  selector: 'app-grid',
  imports: [
    TableModule,
    DirectiveSwitchDirective,
    ContextMenuModule,
    FilterWrapperComponent,
    CommonModule,
  ],
  templateUrl: './grid.component.html',
  styleUrl: './grid.component.scss',
})
export class GridComponent<T extends GridRowData = GridRowData> {
  @ViewChild('dt') dt?: Table;
  @ViewChildren(FilterWrapperComponent)
  filterWrappers?: QueryList<FilterWrapperComponent>;

  readonly columns = input.required<GridColumn<T>[]>();
  readonly data = input.required<T[]>();
  readonly sortable = input<boolean>(false);
  readonly selectionMode = input<'single' | 'multiple' | null>(null);
  readonly filterable = input<boolean>(false);
  readonly pagination = input<boolean>(false);
  readonly resizableColumns = input<boolean>(false);
  readonly columnResizeMode = input<'fit' | 'expand'>('fit');
  readonly reOrderableColumns = input<boolean>(false);
  readonly reOrderableRows = input<boolean>(false);
  readonly virtualScroll = input<boolean>(false);
  readonly contextMenuItems = input<MenuItem[] | undefined>(undefined);
  readonly size = input<'small' | 'large' | undefined>(undefined);
  readonly showGridLines = input<boolean>(false);
  readonly stripedRows = input<boolean>(false);
  readonly rows = input<number>(DEFAULT_PAGE_SIZE);
  readonly rowsPerPageOptions = input<number[]>(DEFAULT_PAGE_SIZE_OPTIONS);
  readonly rowExpandMode = input<'single' | 'multiple'>('multiple');
  readonly rowExpandable = input<boolean>(false);
  readonly emptyMessage = input<string>('No records found');
  readonly expandedRowTemplate = input<TemplateRef<unknown> | null>(null);
  readonly scrollable = input<boolean>(false);
  readonly scrollHeight = input<string | undefined>(undefined);
  readonly totalRecords = input<number>(0);
  readonly lazy = input<boolean>(false);
  readonly hideHeader = input<boolean>(false);
  readonly loading = input<boolean>(false);
  readonly tableStyleClass = input<string | undefined>(undefined);
  readonly styleClass = input<string | undefined>('h-full');
  readonly initialFilters = input<Record<string, FilterValue>>({});
  readonly selection = input<GridSelection<T>>(null);
  readonly visibleColumns = input<string[]>([]);

  readonly pageChange = output<TablePageEvent>();
  readonly rowSelect = output<TableRowSelectEvent>();
  readonly rowUnselect = output<TableRowUnSelectEvent<T>>();
  readonly onLazyLoad = output<TableLazyLoadEvent>();
  readonly onFilterChange = output<FilterChangeEvent>();

  _selectedRows: GridSelection<T> = null;
  expandedRows: GridExpandedRow<T>[] = [];

  // Computed property to get visible columns
  readonly displayedColumns = computed(() => {
    const allColumns = this.columns();
    const visibleColumnFields = this.visibleColumns();

    if (visibleColumnFields.length === 0) {
      return allColumns; // Show all columns if none specified
    }

    return allColumns.filter((col) => visibleColumnFields.includes(col.field));
  });

  constructor() {
    effect(() => {
      const filters = this.initialFilters();
      if (Object.keys(filters).length > 0) {
        // Use setTimeout to ensure ViewChildren are available
        setTimeout(() => {
          this.updateFilterComponents(filters);
        });
      }
    });
  }

  onPageChange(event: TablePageEvent): void {
    this.pageChange.emit(event);
  }

  onRowSelect(event: TableRowSelectEvent): void {
    this.rowSelect.emit(event);
  }

  onRowUnselect(event: TableRowUnSelectEvent<T>): void {
    this.rowUnselect.emit(event);
  }

  toggleRow(rowData: T): void {
    const index = this.expandedRows.findIndex((row) => row.data === rowData);
    if (index === -1) {
      this.expandedRows.push({ data: rowData, expanded: true });
    } else {
      this.expandedRows.splice(index, 1);
    }
  }

  isRowExpanded(rowData: T): boolean {
    return this.expandedRows.some((row) => row.data === rowData);
  }

  onLazyLoadEvent(event: TableLazyLoadEvent): void {
    this.onLazyLoad.emit(event);
  }

  exportCSV(): void {
    this.dt?.exportCSV();
  }

  handleFilterChange(field: string, value: FilterValue): void {
    this.onFilterChange.emit({ field, value });
  }

  private updateFilterComponents(filters: Record<string, FilterValue>): void {
    if (!this.filterWrappers) return;

    this.filterWrappers.forEach((wrapper) => {
      const field = wrapper.field();
      if (field && filters[field] !== undefined) {
        wrapper.setInitialValue(filters[field]);
      }
    });
  }

  /**
   * Sets the selection programmatically
   * @param item The item to select
   */
  setSelection(item: T | T[] | null): void {
    if (isNullOrUndefined(item)) return;

    // Set the selection
    this._selectedRows = item;

    if (isDefined(this.dt)) {
      // Force table selection to update
      setTimeout(() => {
        // This is necessary because PrimeNG's Table sometimes needs
        // a tick to properly process the selection change
        if (this.dt) {
          this.dt.selection = item;
        }

        // For single selection, we may need to find the selected row
        // and trigger the UI update
        if (!Array.isArray(item) && this.selectionMode() === 'single') {
          if (this.dt) {
            // Force update of PrimeNG's internal state
            this.dt.selectionChange.emit(item);
          }
        }
      });
    }
  }

  get selectedRows(): GridSelection<T> {
    return this._selectedRows;
  }
}
